@import "variables.scss";

.heading {
  font-weight: 700;
  font-size: 2.375rem;
  letter-spacing: 1.425px;
  line-height: 3rem;

  &.default {
    color: $blackText;
  }

  &.blue {
    color: $navyBlueText;
  }

  &.primary {
    color: $blackTextPrimary;
  }
}

@media screen and (max-width: 1200px) {
  .heading {
    font-size: 2rem;
    line-height: 2.7rem;
  }
}

@media screen and (max-width: 1024px) {
  .heading {
    font-size: 1.7rem;
    line-height: 2.4rem;
  }
}

@media screen and (max-width: 860px) {
  .heading {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }
}

@media screen and (max-width: 767px) {
  .heading {
    font-size: 2.6rem;
    line-height: 3rem;
  }
}

@media screen and (max-width: 576px) {
  .heading {
    font-size: 2.3rem;
    line-height: 2.7rem;
  }
}

@media screen and (max-width: 480px) {
  .heading {
    font-size: 2.1rem;
    line-height: 2.5rem;
  }
}

@media screen and (max-width: 420px) {
  .heading {
    font-size: 1.75rem;
    line-height: 2.2rem;
  }
}
